import {Component, ViewChild} from '@angular/core';
import {Nav, Platform,AlertController} from 'ionic-angular';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {BookingsPage} from '../pages/bookings/bookings';
import {HomePage} from '../pages/home/home';
import {ListPage} from '../pages/list/list';
import {StartPage} from '../pages/start/start';
import {NotificationsPage} from '../pages/notifications/notifications';
import {ProfilePage} from '../pages/profile/profile';
import {PaymentsPage} from '../pages/payments/payments';
import {PartnerPage} from '../pages/partner/partner';
import {MenuController} from 'ionic-angular';
import {RegisterPage} from "../pages/register/register";
import {SignupPage} from "../pages/signup/signup";
import {FCM} from "@ionic-native/fcm";
import {InAppBrowser, InAppBrowserOptions} from "@ionic-native/in-app-browser";
import {AuthProvider} from "../providers/auth/auth";
import {ProfileProvider} from "../providers/profile/profile";



@Component({
    templateUrl: 'app.html'
})
export class MyApp {
    @ViewChild(Nav) nav: Nav;

    rootPage: any = StartPage;

    enable:any=true;
    pages: Array<{ title: string, component: any }>;

    constructor(public platform: Platform,
                public statusBar: StatusBar,
                public splashScreen: SplashScreen,
                public menuCtrl: MenuController,
                private inAppBrowser: InAppBrowser,
                private _auth: AuthProvider,
                public alertCtrl: AlertController,
                public profileService: ProfileProvider,
                private fcm: FCM
    ) {

        

        if(localStorage.getItem('uid')){
            
            this.enable=false;
          }

          
      
        this.initializeApp();

        // used for an example of ngFor and navigation
        this.pages = [
            {title: 'Home', component: BookingsPage},
            {title: 'Profile', component: ProfilePage},
            {title: 'Payments', component: PaymentsPage},
            {title: 'Notifications', component: NotificationsPage},
            {title: 'Register', component: RegisterPage}
        ];

        

    }

    initializeApp() {


        this.platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
            this.statusBar.styleDefault();
            this.splashScreen.hide();

            this.fcm.subscribeToTopic('partners');

             this.fcm.subscribeToTopic('broadcast');
             this.fcm.getToken().then(token => {
             // backend.registerToken(token);
             });
             this.fcm.onNotification().subscribe(data => {
             alert(data.aps.alert.body);
             if (data.wasTapped) {
             console.info("Received in background");
             } else {
             console.info("Received in foreground");
             }
             ;
             });
             this.fcm.onTokenRefresh().subscribe(token => {
             // backend.registerToken(token);
             });
        });
    }

    closeMenu() {
        this.menuCtrl.close();
    }

    
    
    signOut(): void {
       

        this._auth.signoutUser().then(res => {

           
            this.nav.setRoot(StartPage);
        }).catch(err => {
           
            let alert = this.alertCtrl.create({
                message: err.message,
                buttons: ['OK']
            });
            alert.present();
        });

    }

    openPage(page) {
        // Reset the content nav to have just this page
        // we wouldn't want the back button to show in this scenario
        this.nav.setRoot(this.pages[page].component);
        this.closeMenu();

    }


    // var apple = ''https://dashboard.stripe.com/oauth/authorize?response_type=code&client_id=ca_C43Pb2cbOsFdeelqzwwsJ3xzcSWKxmLv&scope=read_write&state=phani'


}



