import {Component} from "@angular/core";
import {AlertController, IonicPage, LoadingController, NavController, NavParams} from "ionic-angular";
import {AuthProvider} from "../../providers/auth/auth";
import {RegisterPage} from "../register/register";
import {BookingsPage} from "../bookings/bookings";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ProfileProvider} from "../../providers/profile/profile";
import {AngularFireDatabase} from "angularfire2/database";
import {ToggleProvider} from "../../providers/toggle/toggle";

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-login',
    templateUrl: 'login.html',
})
export class LoginPage {

    rForm: FormGroup;
    email: string;
    password: string;
    confirmPassword: string;
    loginPage: any = false;
    forgotPage: any = true;
    registerPage: any = true;
    registrationForm;
    forgotForm;

    constructor(public nav: NavController,
                public navParams: NavParams,
                public authService: AuthProvider,
                public alertCtrl: AlertController,
                public profileService: ProfileProvider,
                public db: AngularFireDatabase,
                public toggleService: ToggleProvider,
                public loadingCtrl: LoadingController, private fb: FormBuilder) {

        this.rForm = fb.group({
            'email': [null, Validators.email],
            'password': [null, Validators.required],
            'validate': ''
        });

        this.registrationForm = fb.group({
            'email': [null, Validators.email],
            'password': [null, Validators.required],
            'confirmPassword': [null, Validators.required]
        });

        this.forgotForm = fb.group({
            'email': [null, Validators.email]
        });


        this.loginPage = false;
        this.forgotPage = true;
        this.registerPage = true;

    }

    showForgot() {
        this.loginPage = true;
        this.forgotPage = false;
    }

    showLogin() {
        this.loginPage = !this.loginPage;
        this.forgotPage = !this.forgotPage;
    }

    showRegister() {
        this.loginPage = !this.loginPage;
        this.registerPage = !this.registerPage;
    }


    login() {
        if (!this.email || !this.password) {
            let alert = this.alertCtrl.create({
                message: 'Please provide email and password',
                buttons: ['OK']
            });
            return alert.present();
        }

        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });
        loading.present();

        this.authService.login(this.email, this.password).then(authData => {
            localStorage.setItem('uid', authData.uid);
            localStorage.setItem('toggleValue','true');
            this.toggleService.toggle(1);
            loading.dismiss();
            this.nav.setRoot(BookingsPage);
        }, error => {
            loading.dismiss();
            let alert = this.alertCtrl.create({
                message: error.message,
                buttons: ['OK']
            });
            alert.present();
        });
        //this.nav.setRoot(HomePage);
    }

    // go to register page

    register() {

        if (!this.email || !this.password || !this.confirmPassword) {
            let alert = this.alertCtrl.create({
                message: 'Please provide an email and password',
                buttons: ['OK']
            });
            return alert.present();
        }

        if (this.password !== this.confirmPassword) {
            let alert = this.alertCtrl.create({
                message: 'Please confirm the password',
                buttons: ['OK']
            });
            return alert.present();
        }


        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });
        loading.present();

        this.authService.register(this.email, this.password).subscribe(authData => {
            loading.dismiss();
            let json = {
                'partnerEmail': this.email,
                'uid': localStorage.getItem('uid'),
                'backgroundCheck':{},
                'isBackgroundClean':false
            };
            this.profileService.registerUser(json).then(
                ref => {
                    console.log(ref)
                    this.nav.setRoot(RegisterPage, {'auth': authData, 'email': this.email})
                }
            )
            // this.nav.setRoot(RegisterPage, {'auth':authData,'email':this.email});
        }, error => {
            loading.dismiss();
            let alert = this.alertCtrl.create({
                message: error.message,
                buttons: ['OK']
            });
            alert.present();
        });
    }

    forgotPassword() {
        if (!this.email) {
            let alert = this.alertCtrl.create({
                message: 'Please provide the email address',
                buttons: ['OK']
            });
            return alert.present();
        }

        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });
        loading.present();

        this.authService.resetPassword(this.email).then(res => {
            loading.dismiss();
            this.nav.setRoot(LoginPage);
        }).catch(err => {
            loading.dismiss();
            let alert = this.alertCtrl.create({
                message: err.message,
                buttons: ['OK']
            });
            alert.present();
        });

    }

}



