import { BrowserModule } from "@angular/platform-browser";
import { ErrorHandler, NgModule } from "@angular/core";
import { IonicApp, IonicErrorHandler, IonicModule } from "ionic-angular";
import { ReactiveFormsModule } from "@angular/forms";
import { MyApp } from "./app.component";
import { HomePage } from "../pages/home/home";
import { ListPage } from "../pages/list/list";
import { LoginPage } from "../pages/login/login";
import { SignupPage } from "../pages/signup/signup";
import { StartPage } from "../pages/start/start";
import { BookingsPage } from "../pages/bookings/bookings";
import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";
import { FCM } from "@ionic-native/fcm";
import { NotificationsPage } from "../pages/notifications/notifications";
import { ProfilePage } from "../pages/profile/profile";
import { PaymentsPage } from "../pages/payments/payments";
import { PartnerPage } from "../pages/partner/partner";
import { RegisterPage } from "../pages/register/register";
import { Camera } from '@ionic-native/camera';
// Import the AF2 Module
import { AngularFireModule } from "angularfire2";
import { AngularFireDatabaseModule } from "angularfire2/database";
import { AuthProvider } from "../providers/auth/auth";
import { AngularFireAuthModule } from "angularfire2/auth";
import { NotificationsProvider } from "../providers/notifications/notifications";
import { BookingsProvider } from "../providers/bookings/bookings";
import { ProfileProvider } from "../providers/profile/profile";
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { BackgroundVerifyProvider } from '../providers/background-verify/background-verify';
import { ToggleProvider } from '../providers/toggle/toggle';

// AF2 DEV Settings
export const firebaseConfig = {
    apiKey: "AIzaSyAZUbCr5oI9z0o89MtaU5urznlGsx5jujg",
    authDomain: "mobilestyler-prod.firebaseapp.com",
    databaseURL: "https://mobilestyler-prod.firebaseio.com",
    projectId: "mobilestyler-prod",
    storageBucket: "mobilestyler-prod.appspot.com",
    messagingSenderId: "896950829266"
};

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    SignupPage,
    StartPage,
    BookingsPage,
    NotificationsPage,
    ProfilePage,
    PaymentsPage,
    PartnerPage,
    RegisterPage

  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    ReactiveFormsModule,
    HttpModule,   
    HttpClientModule

  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    SignupPage,
    StartPage,
    BookingsPage,
    NotificationsPage,
    ProfilePage,
    PaymentsPage,
    PartnerPage,
    RegisterPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    AuthProvider,
    NotificationsProvider,
    BookingsProvider,
    ProfileProvider,
    InAppBrowser,
    FCM,
    Camera,
    BackgroundVerifyProvider,
    ToggleProvider
  ]
})
export class AppModule { }



