import {Injectable} from "@angular/core";
import {AngularFireDatabase} from "angularfire2/database";

/**
 * Bookings Service/Provider
 * @author Phani Mahesh Birudukota
 */
@Injectable()
export class BookingsProvider {

    constructor(public db: AngularFireDatabase) {
    }

    updateBooking(stage: string, booking: any) {
        booking.stage = stage;
        return this.db.list('/booking').update(booking.key, booking);
    }

    getAllBookingsByUid(uid: string) {
        return this.db.list('/booking', ref => ref.orderByChild('partnerId').equalTo(uid));
    }

}



