import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AngularFireDatabase} from "angularfire2/database";
/*
  Generated class for the ToggleProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable() 
export class ToggleProvider {

  constructor(public http: HttpClient,public db: AngularFireDatabase) {
    console.log('Hello ToggleProvider Provider');
  }

  getProfileData(uid) {
    return this.db.list('/partner',ref => ref.orderByChild('uid').equalTo(uid)).valueChanges();
  }
  
  getProfileDataKeys(uid) {
    return this.db.list('/partner',ref => ref.orderByChild('uid').equalTo(uid)).snapshotChanges();
  }

  updateProfileStatus(key,data,status){
    data.status=status;
    this.db.list('/partner').update(key,data);
  }

  toggle(value){
    let json={};
    localStorage.setItem('toggleValue',value);
    if(value)
    localStorage.setItem('toggle','active');
    else
    localStorage.setItem('toggle','inactive');
    this.getProfileData(localStorage.getItem('uid')).subscribe(
      ref=>{
        json=ref[0];
        this.getProfileDataKeys(localStorage.getItem('uid')).subscribe(
          ref=>this.updateProfileStatus(ref[0].key,json,localStorage.getItem('toggle'))
        )
      }

    )
    
  }

}



